/* this is the animation of the logo  */

.os-phrases h2,
.flash_anim h2,
.flash_anim h1 {
  font-weight: 200;
  width: 100%;
  overflow: hidden;
  padding: 0;
  margin: 0;
  position: fixed;
  left: 1%;
  right: 0;
  top: 47%;
  bottom: 0;
  margin: 0 auto;
  text-align: center;
  display: inline-block;
  color: hsla(0, 0%, 0%, 0) !important;
  cursor: pointer;
  z-index: 9000;
}

.blinking {
  -webkit-animation-name: blinkingText;
          animation-name: blinkingText;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: 8s !important;
          animation-delay: 8s !important;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal;
}
/* white theme */

@-webkit-keyframes blinkingText {
  0% {
    color: #000000;
    opacity: 0;
  }
  49% {
    color: #000000;
  }
  60% {
    color: transparent;
  }
  99% {
    color: transparent;
  }
  100% {
    color: #000000;
  }
}

@keyframes blinkingText {
  0% {
    color: #000000;
    opacity: 0;
  }
  49% {
    color: #000000;
  }
  60% {
    color: transparent;
  }
  99% {
    color: transparent;
  }
  100% {
    color: #000000;
  }
}

