  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
/* white theme */

@-webkit-keyframes FadeIn {
  0% {
    opacity: 0;
    text-shadow: 0 0 1px #000000;
  }
  100% {
    opacity: 0.8;
    text-shadow: 0 0 1px #000000;
  }
}

