  0% {
    opacity: 0;
    text-shadow: 0 0 1px #000000;
  }
  100% {
    opacity: 0.8;
    text-shadow: 0 0 1px #000000;
  }
}
/* white theme */

@-webkit-keyframes FadeIns {
  0% {
    opacity: 0;
    text-shadow: 0 0 1px #000000;
    pointer-events: none;
  }
  50% {
    opacity: 0.8;
    text-shadow: 0 0 1px #000000;
    pointer-events: none;
  }
  100% {
    opacity: 0;
    text-shadow: 0 0 1px #000000;
    pointer-events: none;
  }
}

@keyframes FadeIns {
  0% {
    opacity: 0;
    text-shadow: 0 0 1px #000000;
    pointer-events: none;
  }
  50% {
    opacity: 0.8;
    text-shadow: 0 0 1px #000000;
    pointer-events: none;
  }
  100% {
    opacity: 0;
    text-shadow: 0 0 1px #000000;
    pointer-events: none;
  }
}
